import React, { Component } from 'react';

const pics = [
    `${process.env.PUBLIC_URL}/media/image/foto_1.jpg`,
    `${process.env.PUBLIC_URL}/media/image/foto_2.jpg`,
    `${process.env.PUBLIC_URL}/media/image/foto_3.jpg`,
    `${process.env.PUBLIC_URL}/media/image/foto_4.jpg`,
    `${process.env.PUBLIC_URL}/media/image/foto_5.jpg`
]

const data = {
    "Características": `Características:\n- Marca: INTEL\n- Modelo: BX80623I32120`,
    "Especificações": "Especificações:Modelo: 2120 CORE I3 1155\nClock (GHz): 3.30 GHz\nCache: 3M\nArquitetura: 32 nm\nFSB - Front Side Bus: n/a\nSoquete: LGA1155\nCooler: Não\nNº de núcleos: 2\nNº de threads: 4\nTipo de Memoria: DDR3-1066/1333\nGrafico Integrado: Sim\nIntel HD Grafico: Sim\nIntel HD Grafico com frequencia Dinamica: Sim\nGrafico frequencia Base: 850MHz\nMáxima frequência dinâmica da placa gráfica: 1.1 GHz\nIntel Turbo Boost Tecnologia: Não\nIntel Hyper-Threading Tecnologia: Sim\nIntel Virtualization Tecnologia (VT-x): Sim",
    "Conteúdo": "Conteúdo da Embalagem:\n- 01 Processador Intel i3-2120\n- 01 Solução térmica\n- Manual de Instruções ",
    "Garantia": "Tipo de garantia: BALCÃO\nPrazo de garantia: 6 meses"
}

class HeroShop extends Component {

    state = {
        pic: pics[0],
        data: "Características"
    }

    changePic = (type) => {
        const { pic: atual } = this.state;
        const idxAtual = pics.indexOf(atual);
        let idxNext;
        if( type === 'back' ){
            if(idxAtual === 0) idxNext = pics.length - 1;
            else idxNext = idxAtual - 1;
        } else if ( type === 'forw' ){
            if(idxAtual === pics.length - 1) idxNext = 0;
            else idxNext = idxAtual +1;
        }
        this.setState({ pic: pics[idxNext] })
    }

    renderPhotos(){
        return(
            <div className="flex-3 flex horizontal-only">

                <div className="flex-1 flex flex-center">
                    <span 
                        className="set-arrow" 
                        onClick={() => this.changePic('back')}>
                        {"<"}
                    </span>
                </div>

                <div className="flex-5 flex flex-center">
                    <img alt="main cell" src={this.state.pic} className="pic-main" />
                </div>

                <div className="flex-1 flex flex-center">
                    <span 
                        className="set-arrow" 
                        onClick={() => this.changePic('forw')}>
                        {">"}
                    </span>
                </div>

            </div>
        )
    }

    renderData(){
        return(
            <div className="flex-2 flex vertical">


                <div className="painel flex vertical">
                    <div className="painel-head flex horizontal">
                        <div className="flex-1 flex horizontal-only top">
                            <div 
                                className={`flex-1 link ${ this.state.data === 'Características' ? "data-active" : "" }`}
                                onClick={() => this.setState({ data: "Características" })}>
                                <span className="text-center">Características</span>
                            </div>
                            <div 
                                className={`flex-1 link ${ this.state.data === 'Especificações' ? "data-active" : "" }`}
                                onClick={() => this.setState({ data: "Especificações" })}>
                                <span className="text-center">Especificações</span>
                            </div>
                        </div>
                        <div className="flex-1 flex horizontal-only bottom">
                            <div 
                                className={`flex-1 link ${ this.state.data === 'Conteúdo' ? "data-active" : "" }`}
                                onClick={() => this.setState({ data: "Conteúdo" })}>
                                <span className="text-center">Conteúdo</span>
                            </div>
                            <div 
                                className={`flex-1 link ${ this.state.data === 'Garantia' ? "data-active" : "" }`}
                                onClick={() => this.setState({ data: "Garantia" })}>
                                <span className="text-center">Garantia</span>
                            </div>
                        </div>                        
                    </div>
                    <div className="painel-body flex vertical">
                        { data[this.state.data].split('\n').map((item, i) => (<span key={i}>{item}</span> ) )}
                    </div>
                </div>

                <div className="price-container text-center-mb">
                    <h3 className="price"><span>R$</span> 565,00</h3>
                    <span className="price-base">ou 12x de R$ 56,73</span>
                </div>

                <br />

                <button className="button-cta" >
                    ADICIONAR AO CARRINHO
                </button>

            </div>
        )
    }

    render(){
        return (
            <div className="HeroShop">
                
                <div className="container-big flex vertical">


                    <br />

                    <h1 className="text-center titulo">
                        Processador Intel 1155 Core i3-2120 3.3 3MB OeM
                    </h1>
                    <div className="text-center">
                        <span className="color-white star-container" role="img" aria-label="avaliacoes">⭐️⭐️⭐️⭐️⭐️ (45 avaliações)</span>
                    </div>

                    <br /><br />

                    <div className="flex horizontal">
                        { this.renderPhotos() }
                        { this.renderData() }
                    </div>

                    <br /><br />

                </div>

            </div>
        )
    }
}

export default HeroShop;